#root {
    max-width: 70vw;
    min-height: 100vw;
    margin: auto;
    background-color: #9fe870;
    color: #3a341c;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
}

input{
    background-color: #c8f8aa;  
    accent-color: #c8f8aa;
    border-style: 0.5px solid #3a341c;
    border-radius: 5px;
    margin: 10px;
}

input[type=date]{
    text-align: center;
    min-height: 20px;
    min-width: 30px;
    
}

button{
    background-color: #3a341c;
    color: white;
    min-height: 35px;
    min-width: 200px;
    border: none;
    border-radius: 5px;
}

input[type=checkbox]{
    opacity: 0.5;
}

.App-header{
    padding: 10px 0 10px;
    margin: 0 30px 0;
    border-bottom: 2px solid #3a341c;
}

.current-day{
    padding: 20px 0 20px;
    margin: 0 30px 0;
    border-bottom: 2px solid #3a341c;
}

.current-day-text{
    font-size: 4vh;
    font-weight: bolder;
}

.add-15-days{
    margin: 0 30px 0;
    border-bottom: 2px solid #3a341c;
    padding-bottom: 10px;
}

.add-15-container{
    margin: 0 50px 0;
    border-bottom: 4px dotted #3a341c;
    padding-bottom: 20px;
}

.result-text{
    font-weight: bolder;
    font-size: 2vh;
    color: rgb(0, 0, 0);
}